@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
/* Cabin */ /* Ubuntu */

p, button {
    font-family: "Cabin", sans-serif;
}

h1, h2, h3 {
    font-family: "Ubuntu", sans-serif;
    font-weight: bold;
}
body {
    background-image: url(/football-app-front/static/media/background.f1daa933.jpg);
    background-attachment: fixed;
    background-size: cover;
}
:root {
    --brightGreen: #55FF4B;
    --darkGreen: #2A8025;
    --lightGreen: #9CFF96;
}
.doButton  {
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: var(--brightGreen);

    width: 45%;
    max-width: 300px;

    color: var(--darkGreen);
    font-weight: bold;
    font-size: 1rem;
}
.teamCard_container {
    background-color: var(--darkGreen);
    border-radius: 10px;
    margin: 2rem auto;

    width: 40%;
    min-width: 300px;
    max-width: 600px;
    text-align:center;
}

.teamCard_teamName {
    font-size: 2rem;
    color: var(--brightGreen);
    padding-top: 2%;
}

.teamCard_playerName {
    margin: 10px auto;
    padding: 10px;
    border-radius: 5px;
    background-color: var(--brightGreen);

    width: 50%;
    max-width: 590px;

    color: var(--darkGreen);
    font-weight: bold;
}

.teamCard_setScore {
    font-size: 1.5rem;
    font-weight: normal;

    margin-top: 1.5rem;

    color: var(--lightGreen);
}

.teamCard_teamScore {
    font-size: 3rem;
    font-weight: normal;

    margin-top: 0.5rem;

    color: var(--lightGreen);
}

.teamCard_setScoreButton {
    font-size: 3rem;
    color: var(--darkGreen);

    background-color: var(--brightGreen);
    border: none;
    border-radius: 5px;

    padding: 0 1rem;
    margin: 6%;
}
.title {
    color: var(--brightGreen);

    text-align: center;
    font-size: 3rem;

    margin: 1rem;
}

body {
    text-align: center;
}

